<template>
  <div id="app">
    <canvas id="rollingPercentiles" width="1000" height="400"></canvas>
  </div>
</template>

<script>
import Chart from "chart.js";

export default {
  props: {
    chartTitle: { type: String },
    inputLabels: { type: Array },
    inputSeries1: { type: Array },
  },
  data() {
    return {
      ChartData: {
        type: "line",
        data: {
          labels: this.inputLabels,
          datasets: [
            {
              label: "20 MA",
              data: this.inputSeries1.map((x) => {
                return x.moving_average_20;
              }),
              borderColor: ["#385F73"],
              borderWidth: 1,
              borderDash: [10, 5],
              fill: false,
            },
            {
              label: "50 MA",
              data: this.inputSeries1.map((x) => {
                return x.moving_average_50;
              }),
              borderColor: ["orange"],
              borderWidth: 2,
              fill: false,
              hidden: true,
            },
            {
              label: "100 MA",
              data: this.inputSeries1.map((x) => {
                return x.moving_average_100;
              }),
              borderColor: ["green"],
              borderWidth: 1,
              fill: false,
            },

            {
              label: "200 MA",
              data: this.inputSeries1.map((x) => {
                return x.moving_average_200;
              }),
              borderColor: ["purple"],
              borderWidth: 2,
              fill: false,
              hidden: true,
            },
          ],
        },
        options: {
          title: {
            display: true,
            text: this.chartTitle,
          },
          elements: {
            point: {
              radius: 0,
            },
          },
          responsive: true,
          lineTension: 1,
          scales: {
            yAxes: [
              {
                scaleLabel: {
                  display: true,
                  labelString: "Correlation",
                },
                ticks: {
                  beginAtZero: true,
                  padding: 5,
                  userCallback: function (value, index, values) {
                    return value.toFixed(2);
                  },
                },
              },
            ],
          },
        },
      },
    };
  },
  computed: {},
  methods: {
    dev() {
      console.log(this.ChartData);
    },
    createChart(chartId, chartData) {
      const ctx = document.getElementById(chartId);
      const myChart = new Chart(ctx, {
        type: chartData.type,
        data: chartData.data,
        options: chartData.options,
      });
    },
  },

  mounted() {
    this.createChart("rollingPercentiles", this.ChartData);
  },
};
</script>
